#welcome span,
#welcome input {
  font-size: .4rem;
  line-height: 1;
}

#welcome.typing:after {
  content: '|';
  color: #ffc620;
  font-size: .4rem;
  -webkit-animation: blink 1s infinite;
  animation: blink 1s infinite;
}

#welcome .input {
  background: transparent;
  padding: .05rem 0;
  margin: 0;
  width: 0;
  outline: 0;
  color: #7fd6fa;
  border: 0;
  -webkit-transition: width 1s;
  transition: width 1s;
  border-bottom: 2px solid #7fd6fa;
}

#welcome.typing-end .input {
  margin-left: .1rem;
  width: 1.7rem;
}

#welcome.typing-end:after {
  content: '!';
  color: #ffc620;
  font-size: .4rem;
  -webkit-animation: none;
  animation: none;
}

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  51% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  51% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
